// ===============
// CTA 3
// ===============

.bsb-cta-3 {

	.border-start {

		@include media-breakpoint-down(md) {
			border-left: none !important;
		}
	}

	.border-end {

		@include media-breakpoint-down(md) {
			border-right: none !important;
		}
	}

	.border-top {

		@include media-breakpoint-up(md) {
			border-top: none !important;
		}
	}

	.border-bottom {

		@include media-breakpoint-up(md) {
			border-bottom: none !important;
		}
	}
}
